:root{
	--background-logo: rgb(255, 21, 21);
}

.navbar{
	width: 100px;
	background: #020202;
	color: #fefefe;
	text-transform: uppercase;
	font-weight: bold;
	position: fixed;
	left: 0;
	height: 100vh;
}

.navbar .logo{
	background: var(--background-logo);
	display: flex;
	flex-direction: column;
	padding: 5px 7px;
	justify-content: center;
	text-align: center;
}

.navbar .logo img{
	width: 5em;
	margin: auto;
	margin-bottom: 1em;
}

.navbar .logo .shield-logo{
	width: 3em;
	margin-bottom: 0;
}

.navbar .logo span{
	margin-top: 7px;
	text-transform: none;
}

.navbar .triangle {
	width: 0px;
	height: 0px;
	border-style: solid;
	border-width: 30px 50px 0 50px;
	border-color: var(--background-logo) transparent transparent transparent;
	transform: rotate(0deg);
 }

 .navbar ul{
	padding: 12px;
	font-size: 12px;
 }

 .navbar li{
	padding: 15px 5px;
	cursor: pointer;
 }

 .navbar li:hover, .icons{
	color:var(--background-logo);
 }

 .navbar .icons{
	text-align: center;
 }

 .navbar .icons li{
	padding: 10px;
 }

 .navbar > p{
	text-transform: none;
	font-weight: normal;
	margin: 15px 3px;
	margin-top: 50px;
	color:rgb(21, 142, 255);
	font-size: 12px;
	padding: 0 15px;
	display: flex;
	flex-direction: column;
}

 .navbar > p > span{
	margin-top: 15px;
	cursor: pointer;
 }