.slider{
	width: 100%;
	height: 40vw;
	background: radial-gradient(rgb(180, 0, 0), rgb(0, 0, 0), rgb(0, 0, 0));
	position: relative;
}

.slider img{
	width: 100%;
	height: 39vw;
	opacity: 0.2;
}

.movie{
	position: relative;
}

.dots{
	position: absolute;
	top: 50%;
	margin-left: 13%;
	display: flex;
	flex-direction: column;
	gap: 10px;
}

.dot{
	width: 10px;
	height: 10px;
	border-radius: 50%;
	background: rgb(78, 78, 78);
	cursor: pointer;
	z-index: 100;
}

.dot.active{
	background: rgb(156, 156, 156);
}

.movie .description{
	color: white;
	position: absolute;
	top: 20%;
	left: 20%;
	width: 50%;
}

.movie .description h3{
	font-size: 2em;
	text-transform: uppercase;
}

.movie .description h2{
	font-size: 5em;
	text-transform: uppercase;
	letter-spacing: 3px;
}

.movie .description p{
	height: 100px;
	width: 50%;
	line-height: 25px;
	overflow: hidden;
	text-overflow: ellipsis;
	margin-bottom: 2em;
}

.movie span{
	color:rgb(255, 21, 21);
	font-weight: bold;
	text-shadow: 0 0 1px rgb(0, 0, 0), 0 0 5px rgb(255, 21, 21);
	cursor: pointer;
	letter-spacing: 3px;
	font-size: 20px;
}
