.avengers{
	width: 100%;
	padding: 3% 1%;
	margin: 4em 0;
	text-align: center;
}

.avengers > img{
	width: 30em;
	margin: 2em;
}

.avengers .avengerDisplay{
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-wrap: wrap;
	gap: 5px;
}

.avengers .avengerDisplay .avenger{
	width: 100px;
	height: 400px;
	background: black;
	transition: .3s all;
}

.avengers .avengerDisplay img{
	width: 100%;
	height: 100%;
	filter: grayscale(80%);
	cursor: pointer;
	opacity: 0.1;
	transition: .3s all;
}

.avengers .avengerDisplay .avenger:hover{
	width: 300px;
	transition: .3s all;
}

.avengers .avengerDisplay .avenger:hover img{
	filter: grayscale(0);
	opacity: 1;
	transition: .3s all;
}

.avengers .avengerDisplay .avenger:hover span{
	display: block;
	color: #fefefe;
	font-weight: bold;
	font-size: 20px;
	margin-top: 10px;
	margin-bottom: 1em;
}

.avengers .avengerDisplay span{
	display: none;
}
