.main-images{
	width: 50vw;
	display: flex;
	align-items: center;
	justify-content: center;
	margin: auto;
	margin-top: -7em;
	gap: 50px;
}

.main-images img{
	width: 100%;
	height: 100%;
	opacity: 0.7;
}

.main-images div{
	position: relative;
	width: 14em;
	height: 17em;
	cursor: pointer;
}

.main-images .main-img{
	width: 16em;
	height: 20em;
}

.main-images > div > div{
	width: 100%;
	height: 100%;
	background: black;
}

.main-images p{
	position: absolute;
	color: #fefefe;
	padding: 7%;
	z-index: 100;
	display: none;
} 

.main-images > div:hover p{
	display: block;
}

.main-images > div:hover img{
	opacity: 0.3;
}

.main-images h3{
	font-size: 16px;
}

.main-images h2{
	font-size: 20px;
}

